.product__item__container {
  padding: 20px 0px 20px 0px;
}

.wrapper-main {}

.wrapper-main.product__item__wrapper {
  @media screen and (max-width: 1150px) {
    width: auto;
    padding: 0px 10px;
    overflow: hidden;
  }
}

.product__item__style__wrapper {
  display: flex;
  justify-content: space-between;
  gap: 50px;
  min-width: 0;
  padding-top: 30px; //////// THIS CHANGE

  @media screen and (max-width: 940px) {
    flex-direction: column;
    align-items: center;
  }
}

.product__slider__container {
  max-width: 446px;
  height: 400px;
  min-width: 0;

  @media screen and (max-width: 1050px) {
    max-width: 346px;
    height: 300px;
  }

  @media screen and (max-width: 940px) {
    max-width: 446px;
    height: 400px;
    flex-grow: 1;
  }

  @media screen and (max-width: 480px) {
    max-width: 288px;
    height: 340px;
  }
}


.swiper.main__swiper {
  position: relative;
  padding: 20px 0;
  border-radius: 20px;
  padding: 48px 150px;
  background-color: var(--white);
  box-shadow: 0px 0px 38px -9px rgba(0, 0, 0, 0.5);

  @media screen and (max-width: 1050px) {
    padding: 40px 50px;
  }

  @media screen and (max-width: 940px) {
    padding: 48px 150px;
  }

  @media screen and (max-width: 480px) {
    padding: 36px 30px;
  }

  & .swiper-wrapper {
    &>.swiper-slide {
      opacity: 0;
      text-align: center;

      & img {
        max-height: 195px;
        max-width: 100%;
        object-fit: fill;
      }
    }

    &>.swiper-slide-active {
      opacity: 1;
    }
  }

  & .swiper-button-prev,
  & .swiper-button-next {
    position: absolute;
    color: var(--green-light);

    &::before,
    &::after {
      font-size: 32px;
    }

    @media screen and (max-width: 480px) {
      display: none;
    }
  }
}


