.swiper.swiper__thumbs {
  margin-top: 20px;

  & .swiper-wrapper {
    padding: 5px;

    &>.swiper-slide {
      padding: 14px 46px;
      background-color: var(--white);
      border-radius: 20px;
      width: auto !important;
      box-shadow: 0px 0px 16px -9px rgba(0, 0, 0, 0.5);

      @media screen and (max-width: 1050px) {
        padding: 16px 42px;
      }

      @media screen and (max-width: 480px) {
        padding: 16px 30px;
      }

      img {
        max-height: 60px;
        max-width: 44px;

        @media screen and (max-width: 1050px) {
          max-height: 38px;
          max-width: 28px;
        }

        @media screen and (max-width: 940px) {
          max-height: 60px;
          max-width: 44px;
        }

        @media screen and (max-width: 480px) {
          max-height: 30px;
          max-width: 20px;
        }
      }
    }

    @media screen and (max-width: 940px) {
      justify-content: space-around;
    }
  }
}

