.product__info__container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.product__title {
  position: relative;

  &::before {
    position: absolute;
    content: '';
    background-image: url(../img/grenn-style-ico.svg);
    background-repeat: no-repeat;
    background-position: center center;
    width: 32px;
    height: 25px;
    top: 16px;
    left: -38px;
  }

  @media screen and (max-width: 1050px) {
    font-size: 1.563rem;

    &::before {
      top: 4px;
    }
  }

  @media screen and (max-width: 600px) {
    margin-left: 30px;
  }
}

.product__avaliability {
  margin-top: 10px;
  font-size: 0.875rem;
  color: var(--green-dark);
  position: relative;
  padding-left: 18px;

  &::before {
    position: absolute;
    content: '';
    background: url(../img/check-1.svg);
    width: 12px;
    height: 12px;
    top: 4px;
    left: 0px;
  }
}

.product__manufacturer__delivery__container {
  margin-top: 20px;
}

.product__inner__style__wrapper {
  display: flex;
  justify-content: space-between;
  gap: 0px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  padding: 16px 20px 30px;

  @media screen and (max-width: 940px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.product__manufacturer__wrapper {}

.product__manufacturer__title,
.product__payment__title,
.product__delivery__title {
  font-size: 0.875rem;
  font-weight: 700;
}

.product__manufacturer__title {}

.product__manufacturer__image__name {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 10px;

  span {
    display: inline-block;
    vertical-align: middle;
  }
}

.product__payment__title {
  margin-top: 30px;
}

.product__payment__image__wrapper {
  display: flex;
  gap: 12px;
  margin-top: 10px;

  img {
    width: 34px;
    height: 24px;
    object-fit: fill;
  }
}

.product__vertical__style {}

.product__delivery__wrapper {
  padding-left: 30px;
  border-left: 1px solid rgba(0, 0, 0, 0.2);

  @media screen and (max-width: 940px) {
    margin-top: 16px;
    padding-left: 0px;
    padding-top: 16px;
    border-left: none;
    border-top: 1px solid rgba(0, 0, 0, 0.2);
  }

}

.product__delivery__title {}

.product__delivery__text {
  font-size: 0.875rem;
  color: rgba(0, 0, 0, 0.5);
  margin-top: 10px;
  max-width: 220px;
}

.product__delivery__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 18px;
  font-weight: 700;
  padding: 12px 20px 14px 44px;
  background-color: var(--white);
  border: 1px solid var(--orange);
  border-radius: 50px;
  outline: none;

  &:hover a {
    border-bottom: 2px solid var(--orange);
  }

  a {
    color: var(--orange);
    font-weight: 700;
  }

  @media screen and (max-width: 480px) {
    padding-left: 22px;
  }
}

.delivery__btn__img {}

.product__price__container {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  padding-top: 30px;
  margin-top: 30px;

  @media screen and (max-width: 480px) {
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }
}

.product__price {
  font-size: 1.563rem;
  font-weight: 700;
  color: var(--green-light);
}

.product__price__inner__style__container {
  display: flex;
  gap: 20px;
}

.product__buy__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 12px 60px;
  text-align: center;
  background-color: var(--orange);
  border: 1px solid var(--orange);
  border-radius: 50px;

  a {
    color: var(--white);
    font-weight: 700;
  }

  path {
    fill: var(--white);
  }
}

.buy__btn__img {}

.product__weigts_img {}

////////////////////tabs/////////////////

.product__description__container {
  margin-top: 80px;
  margin-bottom: 20px;
}

.description__tabs__wrapper {
  display: flex;
  justify-content: flex-start;
}

.description__tabs__item {
  padding: 16px 80px;
  font-size: 1.25rem;
  font-weight: 700;
  border-radius: 20px 20px 0px 0px;
  text-align: center;
  cursor: pointer;

  &.active {
    background-color: var(--green-dark);
    color: var(--white);
  }

  @media screen and (max-width: 480px) {
    font-size: 0.875rem;
    padding: 16px 40px;
  }
}

.tab__item-1 {}

.tab__item-2 {}

.description__content__wrapper {}

.description__content__item {
  border-radius: 0px 20px 20px 20px;
  box-shadow: 0px 0px 38px -9px rgba(0, 0, 0, 0.5);
  padding: 36px 40px;

  @media screen and (max-width: 570px) {
    padding: 16px 16px;
  }

}

.content__item-1 {
  display: none;

  &.active {
    display: block;
  }
}

.content__inner__title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-top: 20px;
}

.content__text {
  margin-top: 16px;
}

.content__green {
  font-weight: 700;
  color: var(--green-dark);
}

.conent__list__item {
  position: relative;
  margin-top: 12px;
  padding-left: 20px;

  &::before {
    position: absolute;
    content: '';
    background-color: var(--black);
    width: 2px;
    height: 2px;
    border-radius: 50%;
    top: 8px;
    left: 8px;
  }
}

.content__item-2 {
  display: none;
  border-top-left-radius: 20px;

  &.active {
    display: block;
  }

}

.description__content__form {
  display: flex;
  flex-direction: column;
}

.content__input__wrapper {
  display: flex;
  justify-content: space-between;
  gap: 16px;
  flex-grow: 1;

  @media screen and (max-width: 768px) {
    flex-direction: column;
  }
}

.content__input__name,
.content__input__mail,
.content__textarea {
  border: 1px solid rgba(0, 0, 0, 0.1);
  outline: none;
}

.content__input__name {
  padding: 16px 22px;
  border-radius: 50px;
  flex-grow: 1;
}

.content__input__mail {
  padding: 16px 22px;
  border-radius: 50px;
  flex-grow: 1;

}

.content__textarea {
  margin-top: 32px;
  border-radius: 20px;
  padding: 16px 22px;
  height: 150px;
  resize: none;

}

.content__submit__btn {
  margin-top: 32px;
  padding: 14px 40px;
  color: var(--white);
  font-weight: 700;
  background-color: var(--orange);
  text-align: center;
  border: 1px solid var(--orange);
  border-radius: 50px;
  outline: none;
  max-width: 220px;
}